<template>
  <div>
    <v-app-bar color="#fff" light height="70px" dense style="box-shadow: 6px 10px 19px -4px rgba(0,0,0,0.75); position: fixed; z-index: 50; background: #fff;" >
      <!-- <template v-slot:img="{ props }">
          <v-img v-bind="props" gradient="to top right, rgba(19,84,122,.5), rgba(128,208,199,.8)"></v-img>
      </template>-->
     <!--  <v-img id="logo2" src="../../assets/img/lion.png" width="45px" height="68px"  style="cursor: pointer; " @click="homeClick"></v-img> -->
      

      <v-toolbar-title id="toolbar" style="padding: 0; margin: 0;">
        <!-- <img src="" alt="kids" /> -->
        <v-img id="logo2" src="../../assets/img/lib.png" @click="homeClick"></v-img>
        <v-img id="logo"  src="../../assets/img/castillodet2.png"  style="cursor: pointer; padding-left: 0; margin-top: 2px; margin-bottom: 2px;" @click="homeClick"></v-img>
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <!-- <v-text-field v-if="searchPressed" @mouseleave="searchPressed = !searchPressed" prepend-icon="search" v-model="search" label="Search" single-line hide-details></v-text-field>
        <v-expand-x-transition></v-expand-x-transition>
      <v-btn  @mousemove="searchPressed = !searchPressed" v-if="searchPressed===false" icon @click="goSearch">
        <v-icon>mdi-magnify</v-icon>
      </v-btn> -->

      <div class="text-center">
        <v-col class="shrink">
          <v-expand-x-transition>
            <v-menu
              v-model="notification"
              :close-on-content-click="false"
              :nudge-width="200"
              bottom
              offset-y
            >
              <template v-slot:activator="{ on }">
                <v-btn  icon v-on="on">
                  <v-icon>notification_important</v-icon>
                </v-btn>
              </template>

              <v-card max-width="450" max-height="700" class="mx-auto">
                <v-list three-line>
                  <template v-for="(notify, key, index) of notifications">
                    <v-subheader v-if="notify.header" :key="notify.header" v-text="notify.header"></v-subheader>
                    <v-divider v-else-if="notify.divider" :key="index" :inset="notify.inset"></v-divider>
                    <v-list-item v-else :key="notify.title" @click="visitNotification(notify)">
                      <v-list-item-avatar>
                        <v-img :src="notify.avatar"></v-img>
                      </v-list-item-avatar>

                      <v-list-item-content>
                        <v-list-item-title v-html="notify.title"></v-list-item-title>
                        <v-list-item-subtitle v-html="notify.subtitle"></v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>

                  </template>
                </v-list>
              </v-card>
            </v-menu>
          </v-expand-x-transition>
        </v-col>

      </div>
        <!-- Menu signOut -->
      <div >
        <v-col >
          <v-expand-x-transition>
            <v-menu
              v-model="menu"
              :close-on-content-click="false"
              :nudge-width="150"
              max-height="320px"
              bottom
              offset-y
              
            >
              <template v-slot:activator="{ on }">
                <v-list height="60px" class="mb-2" rounded style="background: #fff;">
                  <v-list-item class="user-details" v-on="on" style="cursor: pointer; border-radius: 10px;" >
                    <v-list-item-avatar>
                      <img :src="user.image" :alt="user.lastName" />
                    </v-list-item-avatar>
                    <v-list-item-content class="contenido-appbar">
                      <v-list-item-title>{{ user.name }} {{ user.lastName }}</v-list-item-title> 
                    </v-list-item-content>
                    <v-list-item-action>
                      <v-icon>mdi-menu-down</v-icon>
                    </v-list-item-action>
                  </v-list-item>
                </v-list>
              </template>

              <v-card  id="signout-card"  height="auto" width="auto">
                <v-list>
                  <v-list-item >
                    <v-list-item-avatar>
                      <img :src="user.image" :alt="user.lastName" />
                    </v-list-item-avatar>
                    <v-list-item-content>
                      <v-list-item-title>{{ user.name }} {{ user.lastName }}</v-list-item-title>
                      <v-list-item-subtitle>{{ user.role }} | {{ user.level }}</v-list-item-subtitle>
                      <v-list-item-subtitle>{{ user.age }}</v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>

                <v-divider></v-divider>

                <ListSignOut />

                <!-- <v-card-actions>
                  <v-spacer></v-spacer>

                  <v-btn text @click="menu = false">Cancel</v-btn>
                  <v-btn color="primary" text @click="menu = false">Save</v-btn>
                </v-card-actions> -->
              </v-card>
            </v-menu>
          </v-expand-x-transition>
        </v-col>

      </div>
      <v-app-bar-nav-icon @click="openMenu" id="btn-menu" style="margin-right: 0;"></v-app-bar-nav-icon>
    </v-app-bar>
  </div>
</template>

<script>
import ListSignOut from './ListSignOut';
import { mapState, mapActions } from "vuex";

export default {
  components: {
    ListSignOut
  },
  data: () => ({
    fav: true,
    menu: false,
    message: false,
    hints: true,
    expand: false,
    notification: false,
    search: "",
    searchPressed: false
  }),
  computed: {
    ...mapState(["user", "notifications"])
  },
  mounted() {

  },
  methods: {
    openMenu() {
      this.$emit("open-menu");
    },
    visitNotification(item) {
      alert(item.title);
    },
    goSearch() {
        this.searchPressed = !this.searchPressed
    },
    homeClick(){
      this.$router.push('app1/intranet');
    }
  }
};
</script>

<style>


#btn-menu {
  display: none;
}

#logo2 {
  cursor: pointer; 
  margin-left: 10px;  
  margin-bottom: 4px;
  display: none;
}

.user-details:hover {
   background: rgba(0,0,0, .6);
}

.user-details:hover .v-list-item__content,  .user-details:hover .v-list-item__action i{
  color: #fff;
}


@media screen and (max-width: 900px) {
    #btn-menu{
        display: flex;
    }    
}
/* 
@media screen and (max-height: 1000px) {
    #btn-menu{
        display: flex;
    }    
} */

@media screen and (max-width: 600px) {
    
    #logo{
        display: none;
    }

    #logo2{
        display: block;
    }

    
}

@media screen and (max-width: 500px) {
    
    .user-details .v-list-item__title{
        display: none;
    }
    
}

@media screen and (max-width: 345px) {
    
    #toolbar, #logo2{
        margin-left: 0;
        display: none;
    }    
}


</style>